.scr1 {
  position: relative;
  padding-top: 226px;
  padding-bottom: 55px;

  &__inscr {
    margin-bottom: 10px;
  }

  &__content {
    max-width: 745px;
  }

  &__title {
    margin: 0 0 47px;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.388; /* 50 / 36 */

    strong {
      font-weight: 700;
      font-size: 48px;
      text-transform: uppercase;
    }

    b {
      font-weight: 500;
      font-size: 40px;
    }

    p {
      margin-top: 10px;
    }
  }

  &__btns {
    margin-bottom: 79px;
  }

  &__features {
    max-width: 1071px;
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right center;
    z-index: -1;
  }

  @include max(1650) {
    padding-top: 176px;
    padding-bottom: 25px;

    &__title {
      margin: 0 0 22px;
      font-size: 26px;

      strong {
        font-size: 37px;
      }

      b {
        font-size: 30px;
      }
    }

    &__btns {
      margin-bottom: 49px;
    }
  }

  @include max(1300) {
    &__content {
      max-width: 645px;
    }

    &__title {
      margin-bottom: 15px;
      font-size: 22px;

      strong {
        font-size: 34px;
      }

      b {
        font-size: 26px;
      }
    }

    &__btns {
      margin-bottom: 30px;
    }
  }

  @include max(1200) {
    padding-top: 156px;
  }

  @include max(992) {
    padding-top: 76px;

    &__features {
      display: none;
    }

    &__content {
      max-width: 405px;
    }

    &__title {
      font-size: 18px;

      strong {
        font-size: 24px;
      }

      b {
        font-size: 20px;
      }
    }

    &__btns {
      margin-bottom: 0;
    }
  }

  @include max(768) {
    &__img {
      object-position: left center;
    }
  }

  @include max(470) {
    padding-top: 105px;
    padding-bottom: 51px;

    &__title {
      width: calc(100% + 5px);
      margin-bottom: 300px;
      font-size: 20px;

      strong {
        font-size: 22px;
      }

      b {
        font-size: 22px;
      }
    }

    &__inscr {
      margin-bottom: 26px;
    }

    &__btns {
      text-align: center;
    }
  }
}
