.reviews {
  padding-top: 34px;
  padding-bottom: 87px;

  &__sectionDesc {
    margin-bottom: 30px;
  }

  &__btns {
    margin-bottom: 43px;
    display: flex;
    justify-content: center;
  }

  &__btn {
    padding-left: 60px;
    padding-right: 60px;
    margin-left: auto;
    margin-right: auto;
  }

  &__wrap {
    position: relative;
  }

  &__photosWrap {
    position: relative;
    width: calc(50% - 18px);
  }

  &__photosInner {
    position: relative;
  }

  &__cols {
    display: flex;
    justify-content: space-between;
    padding-top: 43px;
  }

  &__videos {
    padding: 20px;
    margin: -20px;
    overflow: hidden;
  }

  &__video {
    position: relative;
    display: inline-block;
    width: 100%;
    border-radius: 25px;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 101px;
      background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 94.44%);
    }
  }

  &__videoInner {
    width: 100%;

    iframe {
      display: block;
      width: 100%;
      height: 380px;
    }
  }

  &__videoTitle {
    position: absolute;
    bottom: 23px;
    left: 40px;
    right: 40px;
    margin: 0;
    font-weight: 500;
    font-size: 24px;
    color: #FFFFFF;
    z-index: 2;
  }

  &__photos {
    padding: 20px;
    margin: -20px;
    overflow: hidden;
  }

  &__photoLink {
    display: block;
    background-color: #FFFFFF;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.2);
    }

    &_mobile {
      display: none;
    }
  }

  &__photosTitle {
    margin: 0 0 29px;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.4;
  }

  @include max(1440) {
    &__videoInner {
      iframe {
        height: 310px;
      }
    }
  }

  @include max(992) {
    &__photosTitle {
      margin: 0 0 15px;
      font-size: 26px;
    }

    &__cols {
      padding-top: 33px;
    }

    &__videoTitle {
      left: 20px;
      right: 20px;
      font-size: 17px;
    }

    &__videoInner {
      iframe {
        height: 223px;
      }
    }
  }

  @include max(768) {
    padding-bottom: 42px;

    &__photosTitle {
      margin-bottom: 15px;
      font-size: 20px;
    }

    &__cols {
      display: block;
    }

    &__videoTitle {
      left: 10px;
      right: 10px;
      font-size: 16px;
    }

    &__photosWrap {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    &__videosBtns,
    &__photosBtns {
      display: none;
    }

    &__btns {
      margin-bottom: 30px;
    }

    &__btn {
      padding-left: 27px;
      padding-right: 27px;
    }
  }

  @include max(576) {
    &__photoLink {
      &_mobile {
        display: block;
      }

      &_desktop {
        display: none;
      }
    }

    &__video {
      &::after {
        content: none;
      }
    }

    &__videoTitle {
      position: static;
      font-size: 14px;
      color: #000000;
    }

    &__videoInner {
      margin-bottom: 11px;
      border-radius: 25px;
      overflow: hidden;
    }

    &__video {
      overflow: visible;
    }

    &__photoLink {
      box-shadow: none;
    }
  }
}
