.certs {
  &__link {
    display: block;
    background-color: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    transition: box-shadow 0.3s;
    overflow: hidden;

    &:hover {
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
    }
  }

  &__cols {
    display: flex;
  }

  &__img {
    display: block;
  }
}
