.form {
  width: 100%;

  &__wrap {
    display: flex;
    align-items: flex-start;
    margin-bottom: 30px;

    .form__item {
      max-width: 287px;
      margin-right: 22px;
    }
  }

  input[type="submit"] {
    height: 70px;
  }

  input,
  textarea {
    font-family: $form-font;
  }

  .text-input {
    display: block;
    width: 100%;

    input,
    textarea {
      background: none;
      border: none;
      outline: none;
      transition: 0.3s;
      resize: none;

      width: 100%;
      height: 70px;
      padding-left: 34px;
      background-color: #FFFFFF;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15);
      border-radius: 40px;
      @include placeholder(#686868, 14px);

      &:focus {
        @include placeholder(#FFFFFF, 14px);
      }

      &:hover {
        box-shadow: inset 0px 4px 4px rgba($accent, 0.5);
      }
    }

    // textarea {
    //   height: 120px;
    //   padding-top: 16px;
    // }
  }

  // &__title {
  //   margin-bottom: 24px;
  //   font-size: 18px;
  //   font-weight: 600;
  // }

  .accept {
    display: block;

    label {
      display: block;
      width: 100%;
      font-weight: 400;
      font-size: 12px;
      color: #808080;
      cursor: pointer;
      transition: 0.3s;

      & > span {
        position: relative;
        display: inline-block;
        padding-left: 38px;
      }

      .accept__icon {
        position: absolute;
        top: 0;
        left: 3px;
        display: block;
        width: 19px;
        height: 19px;
        background-color: #FFFFFF;
        border: 2px solid $accent;
        transition: background-color 0.3s;

        &::before {
          content: "";
          position: absolute;
          top: calc(50% - 5px);
          left: calc(50% - 5px);
          display: block;
          width: 10px;
          height: 10px;
          background: transparent url("../img/tick.svg") no-repeat center center;
          background-size: contain;
          transition: 0.3s;
          opacity: 0;
        }
      }

      &:hover {
        color: $accent;
      }
    }

    input {
      display: none;
    }

    input:checked + span {
      .accept__icon {
        background-color: $accent;

        &::before {
          opacity: 1;
        }
      }
    }
  }

  @include max(992) {
    input[type=submit] {
      height: 60px;
    }
  }

  @include max(768) {
    &__wrap {
      display: block;
      max-width: 287px;
      margin: 0 auto 40px;

      .form__item {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }

    input[type=submit] {
      width: 100%;
      height: 50px;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    form {
      text-align: center;
    }

    .xbtn_form {
      width: 100%;
      margin-top: 9px;
    }
  }
}
