@charset "UTF-8";
/* stylelint-disable */
@font-face {
  font-family: "TTNorms";
  src: url("../fonts/../fonts/TTNorms-Regular.woff2") format("woff2"), url("../fonts/../fonts/TTNorms-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "TTNorms";
  src: url("../fonts/../fonts/TTNorms-Medium.woff2") format("woff2"), url("../fonts/../fonts/TTNorms-Medium.woff") format("woff");
