  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  height: 100%;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #000000;
}

img {
  height: auto;
  max-width: 100%;
}

p {
  margin: 0;
  padding: 0;
}

[data-object-fit-cover] {
  display: block;
  object-fit: cover;
  object-position: center;
  font-family: "object-fit: cover; object-position: center;";
  width: 100%;
  height: 100%;
}

[data-object-fit-contain] {
  display: block;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: #000000;
}

.site-container {
  flex-grow: 1;
}

.xbtn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #FFFFFF;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

input[type=submit][disabled] {
  cursor: no-drop;
  background: #5E5E5E;
}

.accent-color {
  color: #FE864A;
}

.bg-wallpaper {
  background: #FFFFFF url("../img/bg-wallpaper.jpg") repeat center center;
}

.bg-grey {
  background-color: #F7F7F7;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1472px;
}

.clearfix::after {
  display: table;
  clear: both;
  content: "";
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.icon {
  max-width: 100%;
  max-height: 100%;
  fill: #FE864A;
  transition: 0.5s;
}

.to-top {
  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: 100;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  background: rgba(254, 134, 74, 0.8);
  border-radius: 6px;
  box-shadow: 0px 0px 10px 0px rgba(254, 134, 74, 0.3);
  cursor: pointer;
  transition: 0.5s;
  opacity: 0;
  height: 0;
  overflow: hidden;
  z-index: 250;
}
.to-top .icon {
  width: 12px;
  height: 12px;
  fill: #FFFFFF;
  transform: rotate(-90deg);
}
.to-top .icon:hover {
  opacity: 1;
  box-shadow: 0 0 10px 0 rgba(254, 134, 74, 0.7);
}
.to-top .icon.fixed {
  opacity: 1;
  height: 37px;
}

/* Переопределение стилей yandex map */
[class$=-controls-pane],
[class$=-copyrights-pane] {
  display: none;
}

/* Переопределение стилей swiper */
.swiper-wrapper * {
  box-sizing: border-box;
}

.swiper-slide {
  height: auto;
}

.swiper-button-prev,
.swiper-button-next {
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  outline: none;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  width: 55px;
  height: 55px;
  margin: 0;
  background-color: #FFFFFF;
  border-radius: 50%;
  transition: 0.3s;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  content: "";
  width: 8px;
  height: 14px;
  background-color: transparent;
  background-image: url("../img/arrow.svg");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  border: none;
}

.swiper-button-prev {
  left: -27px;
}

.swiper-button-next {
  right: -27px;
}
.swiper-button-next::after {
  transform: rotate(180deg);
}

.swiper-pagination-lock {
  display: none;
}

.swiper-button-disabled {
  display: none;
}

.inscr {
  display: flex;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.6;
}
.inscr::before {
  content: "";
  display: block;
  width: 17px;
  height: 24px;
  margin-right: 21px;
  margin-top: 4px;
  background-image: url("../img/flag.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.section-title {
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 40px;
  text-align: center;
}

.section-desc {
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
}

.mr-0 {
  margin-right: 0 !important;
}

.logo {
  display: flex;
  align-items: center;
}
.logo__img {
  display: block;
  text-decoration: none;
  width: 207px;
  height: 64px;
  margin-right: 26px;
}
.logo__text {
  font-size: 13px;
  line-height: 1.153;
}

.burger {
  flex-shrink: 0;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background-color: #EF7147;
  border-radius: 6px;
}
.burger__wrap {
  position: relative;
  display: block;
  margin-top: 4px;
  transition: 0.3s;
  z-index: 100;
}
.burger .ham {
  width: 35px;
  cursor: pointer;
  transition: transform 400ms;
  user-select: none;
}
.burger .hamRotate.active {
  transform: rotate(45deg);
}
.burger .hamRotate180.active {
  transform: rotate(180deg);
}
.burger .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #FFFFFF;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.burger .ham1 .top {
  stroke-dasharray: 40 139;
}
.burger .ham1 .bottom {
  stroke-dasharray: 40 180;
}
.burger .ham1.active .top {
  stroke-dashoffset: -98px;
}
.burger .ham1.active .bottom {
  stroke-dashoffset: -138px;
}

.text-underline {
  text-decoration: underline;
}

.tabsNav {
  display: flex;
  justify-content: center;
}
.tabsNav > li {
  text-align: center;
}
.tabsNav > li:not(:last-child) {
  margin-right: 31px;
}
.tabsNav > li a {
  min-width: 320px;
  padding: 12px 15px;
  font-size: 20px;
  line-height: 1.6;
  border-radius: 40px;
}
.tabsNav > li.current .tabsNav__btn::before {
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15);
  background-image: none;
  background-color: #FE864A;
}
.tabsNav > li.current .tabsNav__btn::after {
  opacity: 0;
}

.to-top {
  position: fixed;
  right: 20px;
  bottom: 80px;
  z-index: 100;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 0;
  height: 37px;
  background-color: #FE864A;
  background-image: url("../img/arrow-white.svg");
