  cursor: pointer;
  transition: 0.5s;
  opacity: 0;
  overflow: hidden;
  z-index: 250;
}

.to-top:hover {
  opacity: 1;
}

.to-top.fixed {
  opacity: 0.8;
  width: 37px;
}

