  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input::-moz-placeholder,
.form .text-input textarea::-moz-placeholder {
  color: #686868;
  font-size: 14px;
  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input:-ms-input-placeholder,
.form .text-input textarea:-ms-input-placeholder {
  color: #686868;
  font-size: 14px;
  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input:focus::-webkit-input-placeholder,
.form .text-input textarea:focus::-webkit-input-placeholder {
