  font-size: 14px;
  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input:focus::-moz-placeholder,
.form .text-input textarea:focus::-moz-placeholder {
  color: #FFFFFF;
  font-size: 14px;
  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input:focus:-ms-input-placeholder,
.form .text-input textarea:focus:-ms-input-placeholder {
  color: #FFFFFF;
  font-size: 14px;
  transition: 0.5s;
  font-family: "TTNorms", "Arial", "Helvetica Neue", sans-serif;
}
.form .text-input input:hover,
.form .text-input textarea:hover {
  box-shadow: inset 0px 4px 4px rgba(254, 134, 74, 0.5);
}
.form .accept {
  display: block;
}
.form .accept label {
  display: block;
  width: 100%;
  font-weight: 400;
  font-size: 12px;
  color: #808080;
  cursor: pointer;
  transition: 0.3s;
}
.form .accept label > span {
  position: relative;
  display: inline-block;
  padding-left: 38px;
}
.form .accept label .accept__icon {
  position: absolute;
  top: 0;
  left: 3px;
  display: block;
  width: 19px;
  height: 19px;
  background-color: #FFFFFF;
  border: 2px solid #FE864A;
  transition: background-color 0.3s;
}
.form .accept label .accept__icon::before {
  content: "";
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% - 5px);
  display: block;
  width: 10px;
  height: 10px;
  background: transparent url("../img/tick.svg") no-repeat center center;
  background-size: contain;
  transition: 0.3s;
  opacity: 0;
}
.form .accept label:hover {
  color: #FE864A;
}
.form .accept input {
  display: none;
}
.form .accept input:checked + span .accept__icon {
  background-color: #FE864A;
}
.form .accept input:checked + span .accept__icon::before {
  opacity: 1;
}
