.lead {
  padding-top: 45px;
  padding-bottom: 67px;

  &__wrap {
    padding: 85px 40px 85px 82px;
    background: #F7F7F7 url("../img/form-bg.jpg") no-repeat left center;
    background-size: cover;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
  }

  &__title {
    margin: 0 0 30px;
    max-width: 720px;
    font-weight: 700;
    font-size: 40px;
    text-transform: uppercase;
  }

  &__desc {
    max-width: 760px;
    margin-bottom: 62px;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.4;
  }

  &__formTitle {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 20px;
  }

  @include max(1440) {
    &__wrap {
      padding: 25px 40px 35px;
      background-position: top center;
    }

    &__title {
      max-width: 550px;
      font-size: 30px;
    }

    &__desc {
      max-width: 630px;
      margin-bottom: 22px;
      font-size: 26px;
    }

    &__formTitle {
      margin-bottom: 10px;
      font-size: 17px;
    }
  }

  @include max(992) {
    &__wrap {
      padding: 25px;
    }
  }

  @include max(768) {
    padding-top: 24px;
    padding-bottom: 27px;

    &__title {
      max-width: 500px;
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 1.4;
    }

    &__desc {
      max-width: 500px;
      margin-bottom: 16px;
      font-size: 16px;
    }

    &__formTitle {
      margin-bottom: 13px;
      font-weight: 400;
      font-size: 14px;
      line-height: 1.2;
      text-align: center;
    }
  }

  @include max(576) {
    &__wrap {
      padding: 18px 13px 23px;
      background-image: url("../img/form-bg-mob.jpg");
    }

    &__title {
      width: calc(100% + 6px);
    }
  }
}
